import React from "react";

import { CanvasContainerSettings } from "../../interfaces/CoreInterfaces";

import { scenePath } from "@/app/Routing";

interface CanvasContainerProps {
  /** Object with settings for the parent container of the 3JS canvas */
  containerSettings?: CanvasContainerSettings;
  /** Will use fullscreen class if true. Set to a use useState for dynamic toggle.*/
  fullScreen?: boolean;
  /** Children to render inside the canvas container */
  children: React.ReactNode;
}

/**
 * Returns a container to place the 3JS canvas and other UI elements in. Will apply background and fullscreen settings from projectData.
 *
 * @param projectData - Interface with settings for the parent container of the 3JS canvas
 * @param fullScreen - Will use fullscreen class if true. Set to a use useState for dynamic toggle.
 * @param children - Children to render inside the canvas container
 */
const CanvasContainer: React.FC<CanvasContainerProps> = ({ containerSettings, fullScreen, children }) => {
  const inlineStyles = getBgClass(containerSettings);

  const canvasStyles = getCSSProperties(containerSettings?.styles || { position: "relative", width: "100%", height: "100%" });
  const fsCanvasStyles = getCSSProperties(containerSettings?.fsStyles || { position: "fixed", top: 0, left: 0, zIndex: 0 });

  return (
    <div id="background" style={inlineStyles}>
      <div id="canvas-container" style={fullScreen ? fsCanvasStyles : canvasStyles}>
        {children}
      </div>
    </div>
  );
};

export default CanvasContainer;

/**
 * Returns a CSSProperties object with the styles passed in.
 *
 * @param styles - The styles to convert to CSSProperties
 */
const getCSSProperties = (styles: React.CSSProperties) => {
  const inlineStyles: React.CSSProperties = {};
  Object.assign(inlineStyles, styles);
  return inlineStyles;
};

/**
 * Returns the tailwind and inline styles for the background settings.
 *
 * @param bgSettings - The background settings to get styles from
 */
function getBgClass(bgSettings?: CanvasContainerSettings) {
  let inlineStyles: React.CSSProperties = {};

  if (bgSettings?.bgStyles) {
    inlineStyles = getCSSProperties(bgSettings.bgStyles);
  }

  inlineStyles.width = "100%";
  inlineStyles.height = "100%";

  switch (bgSettings?.type) {
    case "contain":
      inlineStyles.backgroundSize = "contain";
      break;
    case "auto":
      inlineStyles.backgroundSize = "auto";
      break;
    case "cover":
      inlineStyles.backgroundSize = "cover";
      break;
    default:
    case "none":
      break;
  }

  if (bgSettings?.image) {
    // add the image to the inline styles
    inlineStyles.backgroundImage = `url(${scenePath}${bgSettings.image})`;
  } else if (bgSettings?.color) {
    inlineStyles.backgroundColor = bgSettings.color;
  }

  console.log(inlineStyles);

  return inlineStyles;
}
